// uni.request
import {
	request
} from '@dcloudio/uni-app'

// URL
// const BASE_URL = 'http://api.988cj.com'
const BASE_URL = 'https://api.xcbdsc.com'
// const UPLOAD_URL = 'http://121.196.234.102:8080'
export const UPLOAD_URL = 'https://image.xcbdsc.com'

// tokentoken
const whiteList = [
	// '/api/cglist',
	// '/api/citytree',
	// '/api/citylist',
	// '/api/marketlist',
	// '/group1/upload',
	// '/api/countrylist',
	// '/api/user/rs',
	// '/api/user/login',
	// '/api/user/sign',
	// '/api/market/commodity/list',
	// '/api/shop/list',
	// '/api/shop/lottery'
] // 

/**
 * token
*/
const checkToken = (url)=>{
	let status = whiteList.includes(url)
	if (!status){
		// token
		const token = uni.getStorageSync('token');
		return {'Content-Type': 'application/json','Authorization': token}
	}else{
		return {'Content-Type': 'application/json'}
	}
}

// 
const fetch = (url, method, data = {}, headers = {}) => {
	return new Promise((resolve, reject) => {
		uni.request({
			url: `${BASE_URL}${url}`,
			method: method,
			data: data,
			header: checkToken(url),
			success: (res) => {
				const {
					code,
					message
				} = res.data
				if (res.statusCode === 200 || code === 200) {
					resolve(res.data)
				} else if (res.statusCode === 201 || code === 201 || message === '') {
					uni.showModal({
						title: '',
						content: '',
						confirmText: '',
						success: ({
							confirm
						}) => {
							if (confirm) {
								uni.navigateTo({
									url: '/pages/login/login',
								});
							}
						},
					})
				} else {
					if (message) {
						//
						console.log('bad reqest:', message);
						uni.showToast({
							title: message,
							icon: 'none'
						})
					} else {
						//
						console.log('bad reqest:', res.data.error);
						uni.showModal({
							title: '',
							content: '', //+res.data.error,
							showCancel: false
						})
					}
				}
			},
			fail: (err) => {
				reject(err)
				console.log('error', err);
			}
		})
	})
}


