export default {
	data() {
		return {
			shareParams: {
				path: '/pages/index/index',
				title: '',
				imageUrl: '',
				desc: '',
				content: ''
			}
		}
	},
	//
	onShareAppMessage(res) {
		return {
			title: this.shareParams.title, // 
			path: this.shareParams.path, // 
			imageUrl: this.shareParams.imageUrl, // 
			desc: this.shareParams.desc,
			content: this.shareParams.content,
			success(res) {
				uni.showToast({
					title: ''
				})
			},
			fail(res) {
				uni.showToast({
					title: '',
					icon: 'none'
				})
			}
		};
	},
	//
	onShareTimeline(res) {
		return {
			title: this.shareParams.title, // 
			path: this.shareParams.path, // 
			imageUrl: this.shareParams.imageUrl ,// 
			success(res) {
				uni.showToast({
					title: ''
				})
			},
			fail(res) {
				uni.showToast({
					title: '',
					icon: 'none'
				})
			}
		};
	}
}