import request, {
	UPLOAD_URL
} from '@/api/index'
import Compressor from 'compressorjs';

export const useUpload = (opts) => {
	const {
		uploadPath,
		tempFilePaths,
		FormData,
		file,
	} = opts;
	const upload = () => {
		return new Promise(async (resolve, reject) => {
			uni.showLoading({
				title: '',
				mask: true
			})
			switch (getPlatform()){
				case 1:
					break
				case 2:
				    // h5file
					uni.uploadFile({
						url: request.UPLOAD_URL + uploadPath,
						name: 'file',
						file: await compressPictures(file),  // 
						formData: {
							output: 'json2'
						},
						success: (res) => {
							uni.showToast({
								title: '',
							});
							uni.hideLoading();
							resolve(res?.data)
						},
						fail: (err) => {
							console.log(err);
							uni.showToast({
								title: '',
								icon: 'error'
							});
							uni.hideLoading();
						}
					})
					break
				case 3:
					// 
					uni.uploadFile({
						url: request.UPLOAD_URL + uploadPath,
						name: 'file',
						filePath: await compressPictures(file),
						formData: {
							output: 'json2'
						},
						success: (res) => {
							uni.showToast({
								title: '',
							});
							uni.hideLoading();
							resolve(res?.data)
						},
						fail: (err) => {
							console.log(err);
							uni.showToast({
								title: '',
								icon: 'error'
							});
							uni.hideLoading();
						}
					})
					break
				}
			
		})
	}
	return {
		upload
	}
}

